html {
  box-sizing: border-box;
  font-family: "Roboto", sans-serif;
  position: relative;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

a {
  text-decoration: none;
}

img {
  width: 100%;
}

.contenedor {
  max-width: 1200px;
  width: 90%;
  margin: 0 auto;
}

.center-flex {
  display: flex;
  justify-content: center;
  height: 100%;
  align-items: center;
}
.center-flex img {
  width: 70%;
}

.absolute-bottom {
  position: absolute;
  bottom: 0;
  width: 100%;
}

.text-center {
  text-align: center;
}

.header {
  background-image: linear-gradient(rgba(0, 0, 0, 0.3), rgba(174, 176, 58, 0.65)), url(../img/polloalabrasa.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
  background-attachment: fixed;
  height: 55vh;
  position: relative;
  display: flex;
  flex-direction: column;
}
@media (min-width: 465px) {
  .header {
    height: 70vh;
  }
}
@media (min-width: 748px) {
  .header {
    height: 90vh;
  }
}

.enlace-header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 2;
  width: 100%;
  transition: background-color 0.3s ease-in-out;
}

.nav-enlace {
  padding: 10px 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.nav-enlace a img {
  width: 80px;
}
.nav-enlace .enlaces {
  width: 50px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.nav-enlace .enlaces a {
  color: #fff;
  font-size: 1.2rem;
  border-bottom: 2px solid rgba(255, 255, 255, 0);
}
.nav-enlace .enlaces a:hover {
  border-bottom: 2px solid #fff;
}
.nav-enlace .enlaces a:last-of-type {
  font-size: 1.5rem;
  text-decoration: none;
  border-bottom: 2px solid rgba(255, 255, 255, 0);
}

.titulo-oferta {
  font-size: 3.5rem;
}

.card-list {
  display: grid;
  grid-template-columns: 100%;
  gap: 25px;
}
@media (min-width: 458px) {
  .card-list {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 748px) {
  .card-list {
    grid-template-columns: repeat(3, 1fr);
  }
}

.background-black {
  background-color: #1b1b1b;
  color: #fff;
}
.background-black p {
  padding: 15px 0;
  margin: 0;
}

.card-info p {
  font-family: "Oswald", sans-serif;
  font-size: 1.4rem;
}
.card-info p span {
  margin-left: 5px;
  font-size: 1.8rem;
  color: #e78402;
  font-style: italic;
  font-weight: 600;
}

.precio {
  font-family: "Oswald", sans-serif;
}
.precio span {
  color: #e78402;
  font-style: italic;
  font-weight: 600;
  font-size: 1.5rem;
}

.title-carrito {
  font-size: 1.8rem;
  margin: 0;
  margin: 15px 0;
}

.subtitle-lista {
  margin: 0;
  margin-bottom: 10px;
  border-bottom: 5px solid #2c2c2c;
  position: sticky;
  top: 0;
  background-color: #fff;
}

.card-relative {
  position: relative;
}

.text-left {
  text-align: left;
}

.m-0 {
  margin: 0;
}

.mt-1 {
  margin: 10px;
}

.btn-agregar {
  border: none;
  padding: 10px;
  border-radius: 50%;
  color: #fff;
  position: absolute;
  right: -2%;
  bottom: 20%;
  font-size: 2.5rem;
  cursor: pointer;
}

.carrito-responsive {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  overflow: hidden;
  display: flex;
  justify-content: flex-end;
  align-items: flex-end;
  transition: left 0.4s ease-in-out;
  z-index: 3;
}

.text-end {
  text-align: end;
}
.text-end i {
  font-size: 1.5rem;
  cursor: pointer;
}

.formulario {
  width: 100%;
  background-color: #fff;
  padding: 15px;
  height: 100%;
  position: relative;
  overflow-y: scroll;
}
@media (min-width: 450px) {
  .formulario {
    max-width: 450px;
    width: 90%;
  }
}

.title-carrito {
  position: sticky;
  top: 0;
  background-color: #fff;
}

.contenedor-menu {
  width: 100%;
  height: 350px;
  overflow-y: scroll;
  position: relative;
}

.carrito-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  padding: 10px;
  margin-bottom: 10px;
  background-color: #7e7e7e;
  border-radius: 5px;
  color: #fff;
}
.carrito-grid img {
  width: 150px;
  height: 150px;
  border-radius: 10%;
}
.carrito-grid .carrito-info {
  text-transform: capitalize;
  font-size: 1rem;
}

.campo {
  margin: 10px 0;
  display: flex;
  flex-direction: column;
}
.campo input {
  width: 65%;
  border: none;
  border: 2px solid #cacaca;
  border-radius: 5px;
  padding: 5px;
}

.campo-btn {
  display: flex;
  align-items: flex-end;
  justify-content: space-between;
  margin: 10px 0;
}

.btn {
  padding: 8px;
  border-radius: 5px;
  border: none;
  cursor: pointer;
  font-size: 1.2rem;
  font-weight: 300;
}

.btn-cancelar {
  background-color: #fff;
  border: 1px solid #000;
  color: #000;
  transition: background-color 0.3s ease-in-out;
}
.btn-cancelar:hover {
  border: 1px solid #585858;
  background-color: #585858;
  color: #fff;
}

.btn-envio {
  background-color: #37ac28;
  color: #eeeded;
  transition: background-color 0.3s ease-in-out;
}
.btn-envio:hover {
  background-color: #279019;
}

.campo-select {
  display: flex;
  flex-direction: column;
}
.campo-select select {
  width: 60%;
  padding: 5px;
  border-radius: 5px;
  border: 2px solid #cacaca;
}

.absolute-ocultar-carrito {
  position: absolute;
  top: 0%;
  font-size: 2.5rem;
  z-index: 1;
  cursor: pointer;
}

.ocultar {
  left: 100%;
}

.mostrar {
  left: 0%;
}

.d-none {
  display: none !important;
}

.d-block {
  display: block !important;
}

.fondo-negro {
  background-color: rgba(49, 49, 49, 0.26);
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
}

.carrito-mostrar {
  position: relative;
}

#carrito-cantidad {
  color: #000;
  background-color: #fff;
  border-radius: 50%;
  text-align: center;
  width: 25px;
  height: 25px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.1rem;
  position: absolute;
  top: -50%;
  right: -80%;
  background-color: #c71b0f;
  color: #fff;
}

.sticky-top {
  position: sticky;
  top: 0;
  z-index: 5;
  background-color: #fff;
}

.sticky-bottom {
  position: sticky;
  bottom: 0;
}

.background-color-white {
  background-color: #fff;
}

.b-bottom-1 {
  border-bottom: 1px solid #000;
}

.fa-bars {
  font-size: 2.1rem;
  color: #fff;
  cursor: pointer;
}
@media (min-width: 478px) {
  .fa-bars {
    display: none;
  }
}

.input-50 {
  width: 50%;
}

.fas {
  transition: background-color 0.3s ease-in-out;
}

.background-green {
  background-color: #038f0f;
}

.background-red {
  background-color: #c42104;
}